
html {
  font-family: 'Roboto Mono', monospace;
}

.navbar-fixed .nav-content {
    margin-left: 20px;
}
.btn {
    margin: 20px;
}

.page-footer {
    margin-top: 200px;
}

.showcase {
    margin-top: 60px;
}
.card-image {
    width: 300px;
    padding: 20px;
}
.cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    margin-top: 60px;
}
.cards h2 {
    font-size: 1.8em;
    text-align: center;
    margin-top: 0;
}
.cards .ingredients {
    margin: 30px auto;
}
.cards .ingredients li {
    display: inline-block;
}
.cards .delete {
    position: absolute;
    top: 4px;
    right: 4px;
    cursor: pointer;
    color: #aaa;
    font-size: 1.4em;
}


.add-cocktail {
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
.add-cocktail h2 {
    font-size: 2em;
    margin: 20px auto;
}
.add-cocktail .field {
    margin: 20px auto;
    position: relative;
}
.add-cocktail .delete {
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    font-size: 1.4em;
    cursor: pointer;
}


.edit-cocktail {
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
.edit-cocktail h2 {
    font-size: 2em;
    margin: 20px auto;
}
.edit-cocktail .field {
    margin: 20px auto;
    position: relative;
}
.edit-cocktail .delete {
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    font-size: 1.4em;
    cursor: pointer;
}

